@font-face {
    font-family: "Baloo";
    src: url("/public/Baloo Tamma 2.woff2") format("woff2"),
    url("/public/Baloo Tamma 2.ttf") format("ttf");
}

:root {
    --darkPurple: #23192F;
    --brightRed: #E95171;
    --brightRedDark: #c64560;
    --eggShell: #FFFEFC;
    --rustyIron: #A18396;
    --lightGrey: #EEE;
}

body {
    color: var(--darkPurple);
    background-color: var(--eggShell);
    font-size: 14px;
}

h1 {
    background-color: var(--darkPurple);
    padding: 4px 8px;
}

h1 a {
    font-family: Baloo, sans-serif;
    font-size: 18px;
    text-decoration: none;
    color: var(--brightRed);
}

.popup {
    width: 250px;
}

.status {
    background-color: var(--lightGrey);
    padding: 8px;
    font-size: 12px;
}

.grid {
    margin: 12px 4px;
    padding: 12px 8px 4px;
    border: 1px solid var(--rustyIron);
    position: relative;
    font-size: 12px;
}

h2 {
    font-size: 14px;
    position: absolute;
    top: -8px;
    left: 2px;
    background-color: var(--eggShell);
    padding: 0 4px;
}

.grid.vertical {

}

.grid.horizontal {

}

input[type=checkbox]{
    height: 0;
    width: 0;
    visibility: hidden;
}

.checkbox label {
    cursor: pointer;
    text-indent: -999px;
    width: 20px;
    height: 10px;
    background: grey;
    display: block;
    border-radius: 10px;
    position: relative;
}

.checkbox label:after {
    content: '';
    position: absolute;
    top: 1px;
    left: 1px;
    width: 8px;
    height: 8px;
    background: #fff;
    border-radius: 50%;
    transition: 0.3s;
}

.checkbox input:checked + label {
    background: #bada55;
}

.checkbox input:checked + label:after {
    left: calc(100% - 5px);
    transform: translateX(-100%);
}

.checkbox label:active:after {
    width: 13px;
}

.footer {
    display: flex;
    justify-content: center;
    margin: 8px;
}

.footer a {
    font-size: 12px;
    color: var(--rustyIron)
}
